p {
    font-family: "Lato", sans-serif;
    font-weight: 100;
    font-style: normal;
    line-height: 1.5rem;
    font-size: 1rem;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
ul {
    font-family: "Abril Fatface", serif;
    font-weight: 600;
    font-style: normal;
}

ul {
    list-style: inside circle;
    line-height: 1.5rem;
    font-size: 1.1rem;
    padding:0;
    li {
        font-family: "Lato", sans-serif;
        font-weight: 400;
        font-style: normal;
        font-size: 1rem;
        line-height: 1.3rem;
        font-weight: 100;
    }

}